.Header {
  position: fixed;
  border: 2px solid blue;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  max-width: 1400px;
  height: 5rem;
  line-height: 5rem;
  line-height: 5rem;
  padding: 0 1rem; }
  .Header .Logo {
    position: absolute;
    height: 5rem;
    line-height: 5rem;
    z-index: 100; }
    .Header .Logo a {
      display: grid;
      width: auto;
      height: 5rem;
      align-content: center; }
    .Header .Logo img {
      width: auto;
      height: 5rem; }
  .Header .Panel-btn {
    position: absolute;
    z-index: 1000;
    right: .5rem; }
  .Header .Panel {
    position: fixed;
    z-index: 900;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: calc(100% + 5rem);
    overflow-y: auto;
    transform: translate(100%, 0);
    transition: transform .5s ease; }
    .Header .Panel.is-active {
      transform: translate(0, 0); }

.hamburger:hover {
  opacity: 1; }

.hamburger:focus {
  outline: 0;
  border: 0; }

.hamburger-inner, .hamburger-inner::before, .hamburger-inner::after {
  background-color: blue; }

.hamburger.is-active .hamburger-inner,
.hamburger.is-active .hamburger-inner::before,
.hamburger.is-active .hamburger-inner::after {
  background-color: red; }

.Menu {
  margin: 0 auto;
  padding-top: 5rem;
  text-align: center;
  height: calc(100% - 5rem);
  background: green; }
  .Menu ul {
    display: flex;
    flex-direction: column;
    list-style: none; }

.Container-central {
  border: 2px solid green;
  margin-top: 5rem; }

.Main {
  border: 2px solid blue; }

.Footer {
  border: 2px solid brown; }

h1 {
  font-size: 2rem; }

h2 {
  font-size: 1.75rem; }

h3 {
  font-size: 1.5rem; }

h4 {
  font-size: 1.25rem; }

h5, a, abbr, b, code, dfn, em, i, li, mark, p, pre, strong {
  font-size: 1rem; }

h6, small {
  font-size: .75rem; }

html {
  font-family: "Comfortaa", cursive;
  font-size: 1em; }

body {
  font-family: "Comfortaa", cursive; }
